body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.mylist, .recomlist {
  display:-webkit-flex;
  display:flex;
  text-align: center;
}

h3 {
  margin-left: 10px;
}

img {
  width: 166px;
  height: 234px;
}
.titles {
  margin-left: 10px;
}

.film{
  margin: 10px;
}

.btn{
  visibility: hidden;
  z-index: 1;
}

.film:hover .btn{
  visibility: visible;
}
